import {Injectable} from '@angular/core';
import {FormControlSize} from '../form-control/index';
import {PlacementArray} from '../../common/positioning';

@Injectable({
  providedIn: 'root'
})
export class AdaptRxSelectConfigService {
  size: FormControlSize = 'default';
  placement: PlacementArray = ['auto'];
  appendToBody: boolean = false;
  popupMaxHeight: string | number = 250;
  enableFilter: boolean = false;
  emptyOption: boolean = false;
  singleSelectStyle: 'line' | 'marker' = 'line';
  inline: boolean = false;
  selectAllButton: boolean = false;
  deselectAllButton: boolean = false;
  closeOnSelect: boolean = true;
  focusFirst: boolean = true;
}
